.about {
  height: auto;
  margin-bottom: 150px;
  margin-top:250px;
}
.about__title {
  padding-top: 150px;
  color: #1f1f1f;
}
.facts {
  display: flex;
  margin-top: 60px;
  justify-content: space-around;
}
.facts__item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.facts__item-title {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  text-align: center;
  letter-spacing: -1.27273px;
  color: #0ad9c6;
}
.facts__item_dscr {
  margin-top: 30px;
  max-width: 260px;
  text-align: center;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  color: #000000;
}

@media (min-width: 320px) and (max-width: 767px) {
  .about {
    height: auto;
  }

  .facts {
    flex-direction: column;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .facts {
    flex-wrap: wrap;
  }
  .facts__item {
    width: 50%;
  }
}
